import { Streamlit, RenderData } from "streamlit-component-lib"
import './svg-pan-zoom-container'
import { getScale, pan } from './svg-pan-zoom-container';

function onRender(event: Event): void {

  const container = document.getElementById('container')

  const c = container!.firstChild
  if (c !== null) {
    container!.removeChild(c)
  }

  const svgNode = container!.appendChild(document.createElementNS("http://www.w3.org/2000/svg", "svg"))

  const plotWidth = document.body.clientWidth
  
  const plotHeight = 500
  svgNode.setAttribute("width", plotWidth.toString())
  svgNode.setAttribute("height", plotHeight.toString())

  const data = (event as CustomEvent<RenderData>).detail

  const group = document.createElementNS("http://www.w3.org/2000/svg", "g")
  const upperGroup = document.createElementNS("http://www.w3.org/2000/svg", "g")
  const connectionGroup = document.createElementNS("http://www.w3.org/2000/svg", "g")
  const lowerGroup = document.createElementNS("http://www.w3.org/2000/svg", "g")
  const ticksGroup = document.createElementNS("http://www.w3.org/2000/svg", "g")

  upperGroup.setAttribute('fill', 'white')
  connectionGroup.setAttribute('stroke', 'white')
  connectionGroup.setAttribute('stroke-width', '0.2')
  lowerGroup.setAttribute('fill', 'white')

  const connectionHeight = plotHeight / 8
  
  const upperHeight = (plotHeight-connectionHeight) / 2
  const lowerHeight = (plotHeight-connectionHeight) / 2

  const upperPos = 0
  const connectionPos = upperPos + upperHeight
  const lowerPos = connectionPos + connectionHeight

  const json_data = data.args['json']
  
  const audioFilename = data.args['audioFilename']

  const note_data = json_data['noteAnnotations']

  const total_duration = note_data[note_data.length-1].noteEnd as number

  const removeTicks = () => {
    while (ticksGroup.firstChild) {
        ticksGroup.removeChild(ticksGroup.firstChild)
    }
  }

  const addTicks = (scale: number, y: number, orientation: boolean) => {
    for (let t = 0; t < total_duration; t++) {
      const x = t / total_duration * plotWidth
  
      const tickEl = document.createElementNS("http://www.w3.org/2000/svg", "line")
      tickEl.setAttribute('x1', x.toString())
      tickEl.setAttribute('x2', x.toString())
      tickEl.setAttribute('y1', y.toString())
      if (orientation) {
        tickEl.setAttribute('y2', (y-5).toString())
      } else {
        tickEl.setAttribute('y2', (y+5).toString())
      }
      tickEl.setAttribute('stroke-width', `${1.0/scale}`)
      tickEl.setAttribute('stroke', 'white')
  
      const textEl = document.createElementNS("http://www.w3.org/2000/svg", "text")
      textEl.setAttribute('fill', 'white')
      const dy = orientation ? 15 : -15
      textEl.setAttribute('text-anchor', 'middle')
      textEl.setAttribute('font-size', "10")
      textEl.setAttribute('transform', `translate(${x}, ${y+dy}) scale(${1.0/scale}, 1) rotate(45)`)
      textEl.textContent = t.toString()
  
      ticksGroup.appendChild(tickEl)
      ticksGroup.appendChild(textEl)
    }
  }

  addTicks(1, upperPos+upperHeight, true)
  addTicks(1, lowerPos, false)


  const uppperBounds = svgNode.appendChild(
    document.createElementNS("http://www.w3.org/2000/svg", "rect")
  )
  uppperBounds.setAttribute('x', "0")
  uppperBounds.setAttribute('y', upperPos.toString())
  uppperBounds.setAttribute('width', plotWidth.toString())
  uppperBounds.setAttribute('height', upperHeight.toString())
  uppperBounds.setAttribute('fill', 'none')
  uppperBounds.setAttribute('stroke', '#fff')

  const lowerBounds = svgNode.appendChild(
    document.createElementNS("http://www.w3.org/2000/svg", "rect")
  )
  lowerBounds.setAttribute('x', "0")
  lowerBounds.setAttribute('y', lowerPos.toString())
  lowerBounds.setAttribute('width', plotWidth.toString())
  lowerBounds.setAttribute('height', lowerHeight.toString())
  lowerBounds.setAttribute('fill', 'none')
  lowerBounds.setAttribute('stroke', '#fff')

  const noteHeight = upperHeight / 127.0
  
  for (const el of note_data) {
      const expectedBegin = el['expectedNoteBegin'] as number
      const expectedEnd = el['expectedNoteEnd'] as number
      const expectedNotePitch = el['expectedNotePitch'] as number

      const noteBegin = el['noteBegin'] as number
      const noteEnd = el['noteEnd'] as number
      const notePitch = el['notePitch'] as number
      const mistakes = el['mistakes']
     
      const expectedBeginX = expectedBegin / total_duration * plotWidth
      const expectedEndX = expectedEnd / total_duration * plotWidth
      const expectedNoteY = upperHeight * (127-expectedNotePitch) / 127.0

      const beginX = noteBegin / total_duration * plotWidth
      const endX = noteEnd / total_duration * plotWidth
      const noteY = lowerPos + lowerHeight * (127-notePitch) / 127.0

      let rest1 = true

      if (expectedNotePitch != 0) {
        const expectedNoteEl = document.createElementNS("http://www.w3.org/2000/svg", "rect")
        expectedNoteEl.setAttribute('x', expectedBeginX.toString())
        expectedNoteEl.setAttribute('y', expectedNoteY.toString())
        expectedNoteEl.setAttribute('width', (expectedEndX-expectedBeginX).toString())
        expectedNoteEl.setAttribute('height', noteHeight.toString())
        expectedNoteEl.setAttribute('stroke', 'none')
        upperGroup.appendChild(expectedNoteEl)

        const bgEl = document.createElementNS("http://www.w3.org/2000/svg", "rect")
        bgEl.setAttribute('x', expectedBeginX.toString())
        bgEl.setAttribute('y', upperPos.toString())
        bgEl.setAttribute('width', (expectedEndX-expectedBeginX).toString())
        bgEl.setAttribute('height', upperHeight.toString())
        bgEl.setAttribute('fill', '#aaa1')
        bgEl.setAttribute('stroke', '#aaa')
        bgEl.setAttribute('stroke-width', '0.1')
        upperGroup.appendChild(bgEl)

        rest1 = false
      }

      let rest2 = true

      if (notePitch != 0) {
        const noteEl = document.createElementNS("http://www.w3.org/2000/svg", "rect")
        noteEl.setAttribute('x', beginX.toString())
        noteEl.setAttribute('y', (noteY).toString())
        noteEl.setAttribute('width', (endX-beginX).toString())
        noteEl.setAttribute('height', noteHeight.toString())

        if (mistakes === '') {
          noteEl.setAttribute('fill', '#0f0f')
          noteEl.setAttribute('stroke', 'none')
        } else {
          noteEl.setAttribute('fill', '#f00f')
          noteEl.setAttribute('stroke', 'none')
        }

        lowerGroup.appendChild(noteEl)

        const bgEl = document.createElementNS("http://www.w3.org/2000/svg", "rect")
        bgEl.setAttribute('x', beginX.toString())
        bgEl.setAttribute('y', lowerPos.toString())
        bgEl.setAttribute('width', (endX-beginX).toString())
        bgEl.setAttribute('height', lowerHeight.toString())

        bgEl.setAttribute('fill', '#aaa1')
        bgEl.setAttribute('stroke', '#aaa')

        bgEl.setAttribute('stroke-width', '0.1')
        lowerGroup.appendChild(bgEl)

        rest2 = false

        bgEl.onclick = () => {
            Streamlit.setComponentValue(el)
        }
      } else {
        const bgEl = document.createElementNS("http://www.w3.org/2000/svg", "rect")
        bgEl.setAttribute('x', beginX.toString())
        bgEl.setAttribute('y', lowerPos.toString())
        bgEl.setAttribute('width', (endX-beginX).toString())
        bgEl.setAttribute('height', lowerHeight.toString())

        //bgEl.setAttribute('fill', 'none')

        if (mistakes === '') {
            bgEl.setAttribute('fill', '#afa1')
            bgEl.setAttribute('stroke', '#0f0f')
        } else {
            bgEl.setAttribute('fill', '#f00f')
            bgEl.setAttribute('stroke', '#f00f')
        }
        
        bgEl.setAttribute('stroke-width', '0.1')
        lowerGroup.appendChild(bgEl)
      }

      //if (! (rest1 || rest2)) {
        const connectionEl = document.createElementNS("http://www.w3.org/2000/svg", "polygon")

        const points = [
          expectedBeginX,
          connectionPos,
          beginX,
          lowerPos,
          endX,
          lowerPos,
          expectedEndX,
          connectionPos
        ]

        connectionEl.setAttribute('points', points.join(','))
        connectionEl.setAttribute('fill', '#aaa1')
        connectionEl.setAttribute('stroke', '#aaa')

        connectionGroup.appendChild(connectionEl)
      //}
  }

  // {

  //   let confidence = null

  //   if (json_data['confidence']) {
  //     confidence = json_data['confidence']
  //   }

  //   const polylineEl = document.createElementNS("http://www.w3.org/2000/svg", "polyline")
  //   let points = []
  //   let x = 0
  //   let deltaX = plotWidth / confidence.length

  //   for (let c of confidence) {
  //     points.push(x, lowerPos + (1-c)*lowerHeight)
  //     x += deltaX
  //   }
  //   polylineEl.setAttribute('points', points.join(','))
  //   polylineEl.setAttribute('stroke', '#aaa')
  //   group.appendChild(polylineEl)
  // }

  group.appendChild(ticksGroup)
  group.appendChild(upperGroup)
  group.appendChild(connectionGroup)
  group.appendChild(lowerGroup)

  

  svgNode.appendChild(group)

  const observer = new MutationObserver(function (mutations) {
    mutations.forEach(function (mutation) {
      let scale = getScale(container!)
      removeTicks()
      addTicks(scale, upperPos+upperHeight, true)
      addTicks(scale, lowerPos, false)
    });
  });
  
  observer.observe(container!.firstElementChild!, {
    attributes: true,
    attributeFilter: ['transform'],
  });

  let translate = svgNode.createSVGTransform()
  translate.setTranslate(0, 0);
  group.transform.baseVal.insertItemBefore(translate, 0);

  Streamlit.setFrameHeight(plotHeight)
}

// Attach our `onRender` handler to Streamlit's render event.
Streamlit.events.addEventListener(Streamlit.RENDER_EVENT, onRender)

// Tell Streamlit we're ready to start receiving data. We won't get our
// first RENDER_EVENT until we call this function.
Streamlit.setComponentReady()

// Finally, tell Streamlit to update our initial height. We omit the
// `height` parameter here to have it default to our scrollHeight.
Streamlit.setFrameHeight(1000)
